/* Notes.css */

.Notes {
    background-color: rgb(241, 233, 233);
    color: #333;
    padding: 20px;
    margin: -80px 2px;
  }
  
  h3 {
    color: #333;
  }
  
  .modal-content {
    background-color: #f9f9f9;
  }
  
  .form-label {
    color: #333;
  }
  
  .form-control {
    background-color: #f9f9f9;
    border: 1px solid #ccc;
    color: #333;
  }
  
  .form-control:focus {
    background-color: #f9f9f9;
    color: #333;
    border: 2px solid green;
    box-shadow: none;
  }
  
  .btn-primary {
    background-color: #007bff;
    border: none;
    transition: background-color 0.3s ease;
  }
  
  .btn-primary:hover {
    background-color: green;
  }
  