/* AddNote.css */

.add-note-page {
    background-color: #f4f6f9;
    min-height: 100vh;
  }
  
  .container {
    max-width: 600px;
    margin: 20px auto;
    padding: 20px;
    background: white;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  h1 {
    color: #333;
    font-family: 'Arial', sans-serif;
    font-weight: bold;
  }
  
  .add-note-form .form-label {
    color: #555;
  }
  
  .add-note-form .form-control {
    border-radius: 5px;
    border: 1px solid #ddd;
    padding: 10px;
    font-size: 16px;
  }
  
  .add-note-form .form-control:focus {
    border-color: #28a745;
    box-shadow: none;
  }
  
  .add-note-form .btn {
    background-color: #28a745;
    border: none;
    padding: 10px;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .add-note-form .btn:hover {
    background-color: #218838;
  }
  