/* Noteitem.css */

.note-card {
    padding: 15px;
  }
  
  .card {
    border-radius: 10px;
    background-color: #fff;
    border: none;
    margin: -680px 1px;
    transition: transform 0.3s, box-shadow 0.3s;
  }
  
  .card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  }
  
  .card-title {
    color: #333;
    font-family: 'Arial', sans-serif;
    font-weight: bold;
  }
  
  .card-text {
    color: #555;
    font-family: 'Arial', sans-serif;
  }
  
  .fa-trash-alt {
    font-size: 18px;
  }
  
  .fa-edit {
    font-size: 18px;
  }
  