/* Navbar.css */

/* General navbar styling */
.custom-navbar {
    background-color: #343a40;
    color: white;
    padding: 10px;
  }
  
  .navbar-brand {
    padding: 0 15px;
  }
  
  .nav-link {
    color: #fff;
    transition: color 0.3s ease-in-out;
    padding: 10px 15px; /* Ensure consistent padding */
    margin: 0 5px; /* Ensure consistent margins */
  }
  
  .nav-link:hover,
  .nav-link.active-link {
    color: rgb(238, 255, 0);
  }
  
  .color-picker {
    height: 30px;
    width: 30px;
    cursor: pointer;
    margin-left: 10px;
    border-radius: 50%;
    transition: transform 0.2s ease-in-out;
  }
  
  .color-picker:hover {
    transform: scale(1.1);
  }
  
  .btn-outline-success,
  .btn-outline-primary,
  .btn-outline-danger {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-left: 10px;
  }
  
  .btn-outline-success:hover,
  .btn-outline-primary:hover,
  .btn-outline-danger:hover {
    background-color: green;
    color: white;
  }
  