body.login-page {
    background-color: #2c2f33;
    font-family: 'Arial', sans-serif;
  }
  
  .container {
    height: 100vh;
  }
  
  .login-card {
    max-width: 400px;
    width: 100%;
    background-color: #23272a;
    border-radius: 10px;
    border: none;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    padding: 20px;
  }
  
  .login-card h2 {
    color: #ffffff;
    font-size: 1.8rem;
    margin-bottom: 20px;
  }
  
  .form-label {
    font-size: 1rem;
    color: #dcdde1;
  }
  
  .form-control {
    background-color: #2c2f33;
    color: #ffffff;
    border: 1px solid #41454b;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 15px;
  }
  
  .form-control:focus {
    background-color: #2c2f33;
    color: #ffffff;
    border-color: #7289da;
    box-shadow: 0 0 0 0.2rem rgba(114, 137, 218, 0.25);
  }
  
  .btn-primary {
    background-color: #7289da;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s;
    padding: 10px;
    font-size: 1rem;
  }
  
  .btn-primary:hover {
    background-color: #5a6fa8;
  }
  
  .text-light {
    color: #dcdde1;
  }
  